*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  width: 90%;
  min-height: 500px;
  height: 100%;
  background-color: #e4e4e4;
  margin: 10px auto;
}
.container .header{
  width: 100%;
  height: 70px;
  background-color: #52ceb7;
  line-height: 50px;
  text-align: center;
  color: #ffffff;
}
.row-input{
  width: 100%;
  height: 50px;
  display: flex;
  padding: 5px;
  background-color: #75d9c7;
  
}
.row-input input{
  width: 90%;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  padding: 0 10px;
}
.row-input input:focus{
  outline: none;
}
.row-input input::placeholder{
  color: rgba(0,0,0,0.5);
}
button{
  width: 10%;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  font-weight: bolder;
  background-color: #f56548;
  color: #ffffff;
}
.row-input button:focus{
  outline: none;
}
.row-item{
  width: 100%;
  height: 50px;
  display: flex;
  margin: 5px;
}
.row-item h3 {
  width: 80%;
  line-height: 50px;
}